@font-face {
  font-family: "Montserrat";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat-Regular"), local("Montserrat Regular"), url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat-SemiBold"), local("Montserrat SemiBold"), url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat-Bold"), local("Montserrat Bold"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat-Medium"), local("Montserrat Medium"), url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  display: inline-block;
  box-sizing: border-box;
  vertical-align: middle;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  margin: 0 auto;
  max-width: 1170px;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 21px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

@keyframes bounce {
  0% {
    transform: translateY(-200px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
.page {
  height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 28px;
  min-width: 320px;
  color: rgba(44, 46, 63, 0.7);
}

.page__body {
  margin: 0;
}

.page__footer {
  text-align: left;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  box-sizing: border-box;
  padding: 0;
  padding-bottom: 34px;
  padding-right: 10px;
}
@media (max-width: 1023px) {
  .page__footer {
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 41px;
    padding-left: 21px;
    padding-right: 21px;
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .page__footer {
    text-align: center;
    grid-template-columns: 1fr;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button {
  background-color: #fe7865;
  color: #ffffff;
  box-sizing: border-box;
  text-align: center;
  padding: 30px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
}
.button:active {
  opacity: 1;
  background-color: #f17d4b;
}
@media (max-width: 1023px) {
  .button {
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    width: 250px;
    padding-top: 19px;
    padding-bottom: 19px;
  }
}
@media (max-width: 767px) {
  .button {
    width: 288px;
  }
}

.title {
  font-weight: 600;
  font-size: 55px;
  line-height: 80px;
  margin: 0;
  letter-spacing: -1.25px;
  color: #2c2e3f;
  position: relative;
}
@media (max-width: 1023px) {
  .title {
    font-size: 30px;
    line-height: 45px;
    letter-spacing: initial;
    position: initial;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
}

.header {
  min-height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 26px;
}
@media (max-width: 1023px) {
  .header {
    box-sizing: border-box;
    padding: 0;
    padding-top: 17px;
    position: relative;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 17px 0 0;
  }
}

.header__logo {
  margin-right: auto;
}
@media (max-width: 1023px) {
  .header__logo {
    padding-left: 20px;
    width: 44px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    padding-left: 16px;
  }
}

.header__tel {
  display: flex;
  align-items: center;
  color: #141517;
  text-decoration: underline;
  font-size: 18px;
  line-height: 32px;
  padding: 5px 5px 0 5px;
}
@media (max-width: 1023px) {
  .header__tel {
    display: none;
  }
}
.header__tel:hover, .header__tel:focus {
  color: #fe7865;
}
.header__tel:hover .header__tel-svg, .header__tel:focus .header__tel-svg {
  stroke: #fe7865;
}
.header__tel:active {
  opacity: 0.6;
}

.header__tel-svg {
  margin-right: 9px;
  width: 23px;
  height: 24px;
  stroke: #141517;
}

.main-nav {
  z-index: 2;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-right: 67px;
}
@media (max-width: 1023px) {
  .main-nav__list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    align-items: flex-start;
    justify-content: initial;
    box-sizing: border-box;
    min-height: 568px;
    padding-top: 134px;
    background-color: #ffffff;
    margin-right: 0;
  }
}

.main-nav__item:not(:last-child) {
  margin-right: 31px;
}
@media (max-width: 1023px) {
  .main-nav__item {
    width: 100%;
    background-color: #fbf8f8;
  }
  .main-nav__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

.main-nav__link {
  color: #141517;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  padding: 8px 5px 5px;
}
.main-nav__link:hover, .main-nav__link:focus {
  color: #fe7865;
}
.main-nav__link:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .main-nav__link {
    position: relative;
    color: #48494d;
    font-size: 20px;
    line-height: 28px;
    padding: 16px;
    min-width: 100%;
  }
  .main-nav__link::after {
    position: absolute;
    content: "";
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    width: 12px;
    height: 8px;
    background: url("../img/arrow-menu.svg") no-repeat center;
  }
  .main-nav__link:hover, .main-nav__link:focus {
    opacity: 0.6;
  }
  .main-nav__link:active {
    opacity: 0.3;
  }
}

.main-nav__toggle {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 72px;
  height: 50px;
  box-sizing: border-box;
  border: none;
  padding: 0;
  z-index: 1;
  cursor: pointer;
}
.main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    width: 50px;
  }
}
@media (min-width: 1024px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 1023px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle {
    background-color: #fe7865;
  }
  .main-nav--closed .main-nav__toggle::before {
    position: absolute;
    content: "";
    width: 30px;
    height: 30px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url("../img/menu-burger.svg") center no-repeat;
  }
}

@media (max-width: 1023px) {
  .main-nav--opened .main-nav__list {
    display: flex;
    flex-direction: column;
  }
  .main-nav--opened .main-nav__toggle {
    background-color: transparent;
    width: 50px;
    height: 52px;
  }
  .main-nav--opened .main-nav__toggle::before {
    position: absolute;
    content: "";
    width: 22px;
    height: 23px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url("../img/menu-cross.svg") center no-repeat;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav--nojs {
    width: 100%;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
    position: initial;
    min-height: auto;
    padding: 20px 0;
  }
}

.footer-trips {
  position: relative;
}
.footer-trips::after {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 1px;
  height: 189px;
  background-color: #d7d9df;
}
@media (max-width: 1023px) {
  .footer-trips {
    margin-bottom: 30px;
  }
  .footer-trips::after {
    right: -9%;
    height: 95%;
  }
}
@media (max-width: 767px) {
  .footer-trips {
    padding-bottom: 28px;
    margin-bottom: 30px;
  }
  .footer-trips::after {
    right: 0;
    height: 1px;
    width: 100%;
    bottom: 0;
    top: initial;
  }
}

.footer-trips__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;
  color: #141517;
  margin-top: 0;
  margin-bottom: 20px;
}

.footer-trips__list-wrap {
  display: grid;
  grid-template-columns: 34% 32% 32%;
  row-gap: 20px;
}
@media (max-width: 1023px) {
  .footer-trips__list-wrap {
    grid-template-columns: 34% 35% 33%;
  }
}
@media (max-width: 767px) {
  .footer-trips__list-wrap {
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 20px;
    grid-row-gap: 22px;
  }
}

@media (max-width: 767px) {
  .footer-trips__list:nth-child(3) {
    grid-column: 1/-1;
  }
}

.footer-trips__item:not(:last-of-type) {
  margin-bottom: 10px;
}

.footer-trips__link {
  font-size: 14px;
  line-height: 17px;
  color: rgba(20, 21, 23, 0.5);
  display: block;
}

.footer-contact {
  justify-self: center;
}
@media (max-width: 1023px) {
  .footer-contact {
    padding-left: 16%;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .footer-contact {
    text-align: center;
    padding-left: 0;
    margin-bottom: 23px;
  }
}

.footer-contact__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;
  color: #141517;
  margin-top: 0;
  margin-bottom: 20px;
}

.footer-contact__item:not(:last-of-type) {
  margin-bottom: 17px;
}
@media (max-width: 767px) {
  .footer-contact__item:not(:last-of-type) {
    margin-bottom: 20px;
  }
}

.footer-contact__link {
  font-size: 14px;
  line-height: 17px;
  color: #fe7865;
  text-decoration: underline;
  position: relative;
  padding-top: 2px;
}
.footer-contact__link::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
}
.footer-contact__link--email {
  padding-left: 40px;
}
.footer-contact__link--email::before {
  width: 26px;
  height: 20px;
  background: url("../img/icon-email.svg") no-repeat center;
}
@media (max-width: 1023px) {
  .footer-contact__link--email {
    text-decoration: none;
  }
}
@media (max-width: 767px) {
  .footer-contact__link--email {
    margin-left: 10px;
  }
}
.footer-contact__link--tel {
  font-size: 16px;
  line-height: 32px;
  padding-left: 42px;
}
.footer-contact__link--tel::before {
  width: 30px;
  height: 30px;
  background: url("../img/phone-gray.svg") no-repeat center;
}

.footer-offer {
  padding-left: 17%;
  position: relative;
}
.footer-offer::before {
  position: absolute;
  content: "";
  top: 0;
  left: 10px;
  width: 1px;
  height: 189px;
  background-color: #d7d9df;
}
@media (max-width: 1023px) {
  .footer-offer {
    text-align: center;
    grid-column: 1/-1;
    padding-left: 0;
    padding-top: 30px;
  }
  .footer-offer::before {
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #d7d9df;
  }
}

.footer-offer__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;
  color: #141517;
  margin-top: 0;
  margin-bottom: 20px;
}

.footer-offer__text {
  font-size: 14px;
  line-height: 24px;
  color: rgba(20, 21, 23, 0.5);
}

.promo {
  min-height: 860px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 200px;
}
@media (max-width: 1023px) {
  .promo {
    min-height: 344px;
    grid-template-columns: 1fr;
    margin-bottom: 80px;
  }
}
@media (min-width: 1024px) {
  .promo {
    background-image: url("../img/background/promo-desktop.jpg");
  }
}
@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/background/promo-desktop@2x.jpg");
  }
}
@media (min-width: 768px) {
  .promo {
    background-color: #497798;
    background-image: url("../img/background/promo-tablet.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/background/promo-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .promo {
    background: transparent;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .promo {
    background: transparent;
  }
}

.promo__content {
  color: #ffffff;
  grid-column: 2/-1;
  padding-top: 185px;
  padding-left: 115px;
  width: 390px;
  margin: 0;
}
@media (max-width: 1023px) {
  .promo__content {
    padding: 0;
    padding-top: 60px;
    width: 398px;
    grid-column: 1/-1;
    margin: 0 auto;
    text-align: center;
    box-sizing: border-box;
  }
}
@media (max-width: 767px) {
  .promo__content {
    padding: 0;
    width: 100%;
  }
}

.promo__title {
  margin: 0;
  margin-bottom: 28px;
  letter-spacing: -1.1px;
  color: inherit;
}
@media (max-width: 1023px) {
  .promo__title {
    margin-bottom: 15px;
    letter-spacing: initial;
  }
}
@media (max-width: 767px) {
  .promo__title {
    margin-bottom: 20px;
    padding-top: 81px;
    padding-bottom: 82px;
    padding-left: 22px;
    padding-right: 16px;
    box-sizing: border-box;
    min-height: 235px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url("../img/background/promo-mobile.jpg");
    background-color: #497798;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .promo__title {
    background-image: url("../img/background/promo-mobile@2x.jpg");
  }
}

.promo__text {
  line-height: 27px;
  margin: 0;
  margin-bottom: 34px;
  letter-spacing: -0.5px;
}
@media (max-width: 1023px) {
  .promo__text {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .promo__text {
    color: #2c2e3f;
    margin-bottom: 20px;
    padding: 0 16px;
  }
}

.promo__btn {
  width: 340px;
  position: relative;
  text-align: left;
  padding-left: 40px;
}
.promo__btn::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 40px;
  transform: translateY(-50%);
  width: 14px;
  height: 14px;
  background: url("../img/arrow.svg") no-repeat center;
}
@media (max-width: 1023px) {
  .promo__btn {
    width: 250px;
    padding-top: 19px;
    padding-bottom: 19px;
  }
}
@media (max-width: 767px) {
  .promo__btn {
    width: 288px;
    padding-top: 19px;
    padding-bottom: 19px;
  }
}

.catalog {
  margin-bottom: 22px;
}
@media (max-width: 1023px) {
  .catalog {
    margin-bottom: 52px;
    padding: 0 76px;
  }
}
@media (max-width: 767px) {
  .catalog {
    margin-bottom: 2px;
    padding: 0 16px;
  }
}

.catalog__header {
  width: 358px;
  margin-bottom: 55px;
}
@media (max-width: 1023px) {
  .catalog__header {
    width: 343px;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .catalog__header {
    width: 100%;
    margin-bottom: 20px;
  }
}

.catalog__title {
  padding-bottom: 33px;
  margin-bottom: 30px;
}
@media (min-width: 1024px) {
  .catalog__title::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 229px;
    height: 4px;
    background-color: #fe7865;
  }
}
@media (max-width: 1023px) {
  .catalog__title {
    padding-bottom: 0;
    margin-bottom: 20px;
  }
}

.catalog__description {
  margin: 0;
  letter-spacing: -0.47px;
}

.catalog__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  column-gap: 60px;
}
@media (max-width: 1023px) {
  .catalog__list {
    column-gap: 40px;
  }
}

.catalog__item {
  width: 350px;
  min-height: 430px;
  margin-bottom: 100px;
}
.catalog__item:last-child {
  margin-left: 60px;
}
@media (max-width: 1023px) {
  .catalog__item {
    width: 288px;
    min-height: 425px;
    margin-bottom: 40px;
  }
  .catalog__item:last-child {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .catalog__item {
    margin-bottom: 35px;
  }
}

.catalog__figure {
  margin: 0;
  position: relative;
}

.catalog__caption {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  letter-spacing: -0.42px;
  color: #2c2e3f;
  background-color: #ffffff;
  box-sizing: border-box;
  width: 215px;
  max-height: 425px;
  min-height: 61px;
  padding-top: 17px;
  padding-bottom: 16px;
  overflow: hidden;
}

.catalog__img {
  display: block;
  width: 350px;
  height: 430px;
}
@media (max-width: 1023px) {
  .catalog__img {
    width: 288px;
    height: 425px;
  }
}

.catalog__link {
  position: relative;
}
.catalog__link:hover::after, .catalog__link:focus::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 21, 23, 0.6);
}
.catalog__link:hover .catalog__mask, .catalog__link:focus .catalog__mask {
  opacity: 1;
}

.catalog__mask {
  position: absolute;
  opacity: 0;
  box-sizing: border-box;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
  width: 100%;
  max-width: 266px;
  padding: 30px 23px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  z-index: 1;
  transition: 0.3s ease-in;
}

.places {
  position: relative;
  margin: 0 auto;
  margin-bottom: 234px;
}
@media (min-width: 1024px) {
  .places {
    max-width: 1444px;
  }
  .places::after {
    position: absolute;
    content: "";
    top: 122px;
    left: 0;
    width: 100%;
    height: 1104px;
    background-image: url("../img/background/europe.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    z-index: -1;
  }
}
@media (max-width: 1023px) {
  .places {
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .places {
    margin-bottom: 48px;
  }
}

@media (max-width: 1023px) {
